import React from 'react';
import PropTypes from 'prop-types';
import { getIndicatorProfileUrl } from '../../lib/settings';
import { FormattedMessage } from 'react-intl';

const createLink = indId => {
  const url = getIndicatorProfileUrl + '/' + indId;
  return url;
};

const IndicatorLinkUnicef = ({ data, selItems }) => {
  let indicators = null;
  if (selItems) {
    selItems.forEach(element => {
      if (element.id == 'INDICATOR') indicators = element.values;
    });
  }
  if (!indicators) {
    return null;
  }
  const items = [];
  indicators.forEach(el => {
    items.push(
      <span key={el.id} className="indicatorEntry">
        <a href={createLink(el.id)}>{el.label}</a>
      </span>,
    );
  });

  return (
    <div className="div_IndicatorLink">
      <span className="goToIndic">
        <FormattedMessage id="de.IndicatorLinkUnicef.goToIndicator" />
      </span>

      {items}
    </div>
  );
};

IndicatorLinkUnicef.propTypes = {
  data: PropTypes.object,
  selItems: PropTypes.array,
};

export default IndicatorLinkUnicef;
