'use strict';

export const mapStructure = (s, lang) => {
  const ret = {};
  ret.meta = {
    'content-languages': [lang],
    schema:
      'https://raw.githubusercontent.com/sdmx-twg/sdmx-json/develop/structure-message/tools/schemas/1.0/sdmx-json-structure-schema.json',
  };

  ret.data = {};

  //Dataflow
  ret.data.dataflows = s.Dataflow;
  renameKey(ret.data.dataflows[0], 'agencyId', 'agencyID');
  ret.data.dataflows[0].name = convertName(s.Dataflow[0].names);
  delete s.Dataflow[0].names;
  ret.data.dataflows[0].structure = s.Dataflow[0].dataStructureRef;
  ret.data.dataflows[0].links = createLinkNode('self', s.Dataflow[0].urn, 'dataflow');
  ret.data.dataflows[0].links = ret.data.dataflows[0].links.concat(
    createLinkNode('structure', s.Dataflow[0].dataStructureRef, 'datastructure'),
  );

  //conceptscheme
  ret.data.conceptSchemes = s.ConceptScheme;
  ret.data.conceptSchemes[0].name = convertName(s.ConceptScheme[0].names);
  delete ret.data.conceptSchemes[0].names;
  renameKey(ret.data.conceptSchemes[0], 'agencyId', 'agencyID');
  renameKey(ret.data.conceptSchemes[0], 'items', 'concepts');

  ret.data.conceptSchemes[0].concepts.forEach(v => {
    v.name = convertName(v.names);
    delete v.names;
  });
  ret.data.conceptSchemes[0].links = createLinkNode(
    'self',
    s.ConceptScheme[0].urn,
    'conceptscheme',
  );

  //codelists
  ret.data.codelists = s.Codelist;

  ret.data.codelists.forEach(cl => {
    renameKey(cl, 'agencyId', 'agencyID');
    cl.name = convertName(cl.names);
    delete cl.names;
    renameKey(cl, 'items', 'codes');
    cl.links = createLinkNode('self', cl.urn, 'codelist');

    if (cl.codes) {
      let hasUniqueRoot = isUniqueRoot(cl.codes);
      cl.codes.forEach(c => {
        c.name = convertName(c.names);
        delete c.names;
        if (c.parentCode && !hasUniqueRoot) {
          renameKey(c, 'parentCode', 'parent');
        }
        c.links = createLinkNode('self', c.urn, 'code');
      });
    }
  });

  /*
      ret.data.dataStructures = s.DataStructure;
      ret.data.dataStructures[0].dataStructureComponents = {};
      ret.data.dataStructures[0].dataStructureComponents.dimensionList = s.DataStructure[0].dimensionList;
  
      */
  //dataStructures
  ret.data.dataStructures = s.DataStructure;
  renameKey(ret.data.dataStructures[0], 'agencyId', 'agencyID');
  ret.data.dataStructures[0].dataStructureComponents = {};
  ret.data.dataStructures[0].name = convertName(ret.data.dataStructures[0].names);
  delete ret.data.dataStructures[0].names;
  ret.data.dataStructures[0].links = createLinkNode(
    'self',
    ret.data.dataStructures[0].urn,
    'datastructure',
  );
  //dataStructures - DimensionsList
  ret.data.dataStructures[0].dataStructureComponents.dimensionList =
    s.DataStructure[0].dimensionList;
  var pos = 0;
  var timePos = -1;
  ret.data.dataStructures[0].dimensionList.dimensions.forEach(d => {
    d.links = createLinkNode('self', d.urn, 'dimension');
    d.type = 'Dimension';
    renameKey(d, 'concept', 'conceptIdentity');
    d.position = pos++;
    if (d.representation && d.representation.representation) {
      d.localRepresentation = { enumeration: d.representation.representation };
    }
    if (d.isTimeDimension) {
      timePos = pos - 1;
      ret.data.dataStructures[0].dimensionList.timeDimensions = [
        {
          id: d.id,
          urn: d.urn,
          links: createLinkNode(d.links[0].rel, d.links[0].urn, d.links[0].type),
          position: d.position,
          type: d.type,
          conceptIdentity: d.conceptIdentity,
          localRepresentation: {
            textFormat: {
              textType: 'ObservationalTimePeriod',
              isSequence: false,
              isMultiLingual: false,
            },
          },
        },
      ];
    }
  });

  if (timePos != -1) ret.data.dataStructures[0].dimensionList.dimensions.splice(timePos, timePos);
  delete ret.data.dataStructures[0].dimensionList;

  //dataStructures - measureList
  ret.data.dataStructures[0].dataStructureComponents.measureList = {};
  ret.data.dataStructures[0].dataStructureComponents.measureList.primaryMeasure =
    s.DataStructure[0].primaryMeasure;
  ret.data.dataStructures[0].dataStructureComponents.measureList.primaryMeasure.links = createLinkNode(
    'self',
    ret.data.dataStructures[0].dataStructureComponents.measureList.primaryMeasure.urn,
    'primarymeasure',
  );
  renameKey(
    ret.data.dataStructures[0].dataStructureComponents.measureList.primaryMeasure,
    'concept',
    'conceptIdentity',
  );
  renameKey(
    ret.data.dataStructures[0].dataStructureComponents.measureList.primaryMeasure,
    'representation',
    'localRepresentation',
  );

  /* ret.data.dataStructures[0].dataStructureComponents.measureList.primaryMeasure.localRepresentation={
           "textFormat": {
               "textType": "Double",
               "sequence": false
           }
       };*/
  delete ret.data.dataStructures[0].primaryMeasure;

  //dataStructures - attributeList
  ret.data.dataStructures[0].dataStructureComponents.attributeList =
    s.DataStructure[0].attributeList;

  if (ret.data.dataStructures[0].dataStructureComponents.attributeList) {
    ret.data.dataStructures[0].dataStructureComponents.attributeList.attributes.forEach(a => {
      if (a.mandatory) {
        a.assignmentStatus = 'Mandatory';
      } else {
        a.assignmentStatus = 'Conditional';
      }
      a.attributeRelationship = {
        dimensions: a.dimensionReferences,
      };
      renameKey(a, 'concept', 'conceptIdentity');
      a.links = createLinkNode('self', a.urn, 'dataattribute');
      if (a.representation && a.representation.representation)
        a.localRepresentation = { enumeration: a.representation.representation };
    });
  }

  delete ret.data.dataStructures[0].attributeList;

  /*console.log('RETURNING STRUCTURE');
  console.log(JSON.stringify(ret));
  console.log(ret);*/

  return ret;
};

function isUniqueRoot(codes) {
  if (!codes) {
    return false;
  }
  let noParentCount = 0;
  //two roots
  for (let i = 0; i < codes.length; i++) {
    if (!codes[i].parentCode) {
      noParentCount++;
    }
    if (noParentCount > 1) return false;
  }
  return true;
}

const convertName = names => {
  const ret = {};
  names.forEach(v => {
    ret[v.locale] = v.value;
  });
  return ret;
};

const renameKey = (object, oldKey, newKey) => {
  if (!object[oldKey]) return;
  object[newKey] = object[oldKey];
  delete object[oldKey];
};

const createLinkNode = (rel, urn, type) => {
  return [{ rel: rel, urn: urn, type: type }];
};

/*
DATA
*/

const convertLanguageNode = (text, lang) => {
  if (text == null) return null;
  var ret = {};
  ret[lang] = text;
  return ret;
};

const convertCodelist = (cl, lang) => {
  cl.name = convertLanguageNode(cl.name, lang);
  cl.roles = [cl.id];
  if (cl.values) {
    cl.values.forEach(v => {
      v.name = convertLanguageNode(v.name, lang);
    });
  }
};

const convert_struct_component = (comp, lang) => {
  if (comp) {
    comp.forEach(v => {
      convertCodelist(v, lang);
      /*v.relationship= {
                    "primaryMeasure": "OBS_VALUE"
                  };*/
    });
  }
};

export const mapData = (d, lang) => {
  const ret = {};
  //meta
  ret.meta = d.header;
  ret.meta['content-languages'] = [lang];

  if (!d.dataSets[0].observations) {
    return null;
  }

  //data
  ret.data = {};
  ret.data.dataSets = d.dataSets;
  ret.data.structure = {};

  ret.data.structure.name = {};
  ret.data.structure.name[lang] = d.structure.name;
  ret.data.structure.description = convertLanguageNode(d.structure.description);
  ret.data.structure.dimensions = d.structure.dimensions;
  ret.data.structure.attributes = d.structure.attributes;
  ret.data.structure.attributes.dataSet = d.structure.attributes.dataset;

  convert_struct_component(ret.data.structure.dimensions.dataset, lang);
  convert_struct_component(ret.data.structure.dimensions.series, lang);
  convert_struct_component(ret.data.structure.dimensions.observation, lang);
  convert_struct_component(ret.data.structure.attributes.dataset, lang);
  convert_struct_component(ret.data.structure.attributes.series, lang);
  convert_struct_component(ret.data.structure.attributes.observation, lang);

  //let toParse = true;
  for (let k in ret.data.dataSets[0].observations) {
    if (ret.data.dataSets[0].observations.hasOwnProperty(k)) {
      if (
        ret.data.dataSets[0].observations[k][0] &&
        !isNaN(ret.data.dataSets[0].observations[k][0])
      ) {
        ret.data.dataSets[0].observations[k][0] = parseFloat(
          ret.data.dataSets[0].observations[k][0],
        );
      }
    }
  }

  return ret;
};
