import React from 'react';
import PropTypes from 'prop-types';

const TableMessageUnicef = ({ returnedKeyCount, limitedTo }) => {
  if (!returnedKeyCount) {
    return null;
  }
  if (!limitedTo) {
    return null;
  }
  if (limitedTo == 0) return null;
  return (
    <div className="div_dataLimited">
      <p>
        Your selection returned {returnedKeyCount} values, the visualization has been limited to{' '}
        {limitedTo} values to avoid slowing down your browser.
      </p>
      <p>Please use the &quot;Download&quot; button to download all the data in CSV format.</p>
    </div>
  );
};

TableMessageUnicef.propTypes = {
  returnedKeyCount: PropTypes.number,
  limitedTo: PropTypes.number,
};

export default TableMessageUnicef;
